.Cards{
    width: 12rem;
    height: 15rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background: rgba(255,255,255,0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 1rem;
    padding: 0px 26px 2rem 26px;
}
.Cards span:nth-of-type(2){
    color: var(--gray);
    font-size: 1rem;
}
.Cards img{
    transform: scale(0.6);
    margin-bottom: -2rem;
}
.c-button{
    background: #ffffff;
    box-shadow: 0px 19px 60px rgba(0,0,0,0.08 );
    border-radius: 7px;
    border: none;
    padding: 5%;
    font-size: 1rem;
    color: #5290FD;
    cursor: pointer;
}
.c-button:hover{
    color: white;
   background-color : #5290FD;
}
@media only screen and (max-width: 600px){
    .Cards{
        width: auto;
    }
}